// MD
import React from 'react';
import { graphql } from 'gatsby';
import { defineCustomElements } from "@deckdeckgo/highlight-code/dist/loader";
import Layout from '@cmp/layout';
import '@style/global.less';
import './article.less';
// import Helmet from "react-helmet";

defineCustomElements();

// In Gatsby, query variables can only be used inside of page queries. (You cant use them with the useStaticQuery hook.)
// $articlePath  createPage  context
export const postQuery = graphql`
  query BlogPostByPath($articlePath: String) {
    markdownRemark(fields: { articlePath: { eq: $articlePath } }) {
      fields {
        articlePath
      }
      html
      id
      parent {
        ... on File {
          name
          base
          mtime
          sourceInstanceName
        }
      }
    }
  }
`;

function Template({ data }) {
  const { markdownRemark: post } = data;
  return (
    <Layout className="page page-article-template">
      <article className='article-wrap' dangerouslySetInnerHTML={{ __html: post.html }}></article>
    </Layout>
  );
}

export default Template;
